import React from 'react';
import styles from './Button.module.css';

export function Button({ 
  children, 
  variant = 'default',
  className = '',
  icon: Icon,
  iconProps = { width: 20, height: 20 },
  ...props 
}) {
  if (!['default', 'primary'].includes(variant)) {
    console.warn(`Unknown button variant: ${variant}. Using default.`);
    variant = 'default';
  }

  return (
    <button
      className={`${styles.button} ${styles[variant]} ${Icon ? styles.withIcon : ''} ${className}`}
      {...props}
    >
      {Icon && <Icon {...iconProps} className={styles.icon} />}
      {children}
    </button>
  );
}